

.logintype {
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  /*background: #e3fff6;*/
  /*background: #ebf5fc;*/
}

.forml {
  border-radius: 20px;
  padding: 30px;
  min-width: 500px;
  display: flex;
  flex-direction: column;
  box-shadow: 7px 7px 20px rgba(0, 0, 0, 0.1),
              -7px -7px 20px rgba(241, 255, 255, 1);

  align-items: center;
}


.forml h1 {
  text-align: center;
  width: 100%;
  margin-bottom: 20px;
  
}

.input-wrapper {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
  width: 350px;
}

.input-wrapper label {
  margin-bottom: 5px;
  font-weight: bold;
  width: 100%;
  color:white;
}

.input-wrapper input {
  background-color: #5082a3;
  border: unset;
  border-radius: 10px;
  padding: 10px;
  outline: none;
  box-shadow: inset 3px 3px 5px rgba(0, 0, 0, 0.1),
  inset -3px -3px 5px rgba(241, 255, 255, 0.5);
}

lbutton {
  margin-top: 20px;
  width: 130px;
  background-color: transparent;
  outline: none;
  color: #292929;
  font-weight: bold;
  border-radius: 10px;
  padding: 10px;
  transition: ease all 250ms;
  box-shadow: 7px 7px 20px rgba(0, 0, 0, 0.1),
  -7px -7px 20px rgb(241, 255, 255);
  border: unset;
}

button:hover {
  cursor: pointer;
  color: #292929;
  box-shadow: inset 3px 3px 5px rgba(0, 0, 0, 0.1),
  inset -3px -3px 5px rgba(241, 255, 255, 0.5);
}

.error {
  margin-top: 10px;
  font-weight: bold;
  font-style: italic;
  color: #b60000;
}